.main-page {

  // .main-page__slider
  &__slider {
    margin-top : 20px;
  }

  // .main-page__collections
  &__collections {
    margin-top : 65px;
  }

  // .main-page__popular-categories
  &__popular-categories {
    margin-top : 45px;
  }

  // .main-page__hits
  &__hits {
    margin-top : 45px;
  }

  // .main-page__advantages
  &__advantages {
    width       : 100vw;
    margin-top  : 80px;
    margin-left : calc((100vw - #{$contentWidth}) / -2);
  }

  // .main-page__about
  &__about {
    margin-top : 80px;
  }

  // .main-page__works
  &__works {
    width       : 100vw;
    margin-top  : 45px;
    margin-left : calc((100vw - #{$contentWidth}) / -2);
  }

  // .main-page__banner
  &__banner {
    margin-top : 80px;
  }

  // .main-page__new
  &__new {
    margin-top : 45px;
  }

  // .main-page__reviews
  &__reviews {
    margin-top : 65px;
  }

}

.collections-block {


  // .collections-block__title
  &__title {
  }

  // .collections-block__inner
  &__inner {
    display               : grid;
    grid-template-columns : repeat(4, 1fr);
    gap                   : 24px;
    grid-auto-flow        : dense;
  }

  // .collections-block__item
  &__item {
    &:nth-child(1) {
      grid-column : 1 / 3;
      grid-row    : 1 / 3;
    }

    &:nth-child(2) {
      grid-column : 3 / 4;
      grid-row    : 1 / 2;
    }

    &:nth-child(3) {
      grid-column : 4 / 5;
      grid-row    : 1 / 2;
    }
  }

  // .collections-block__more
  &__more {
    font-size            : 18px;
    font-weight          : 600;
    line-height          : 1.2222;
    position             : relative;
    left                 : 50%;
    display              : inline-flex;
    align-items          : center;
    margin-top           : 30px;
    transform            : translateX(-50%);
    color                : #161616;
    text-decoration-line : underline;


    &:active,
    &:focus,
    &:hover {
      text-decoration : none;
    }

    svg {
      margin-right : 10px;
    }
  }
}


.item-collection {
  position : relative;
  overflow : hidden;

  // .item-collection--big
  &--big {
  }

  // .item-collection__title
  &__title {
    font-size        : 22px;
    font-weight      : 800;
    line-height      : 1.2227;
    position         : absolute;
    z-index          : 1;
    top              : 10px;
    left             : 0;
    overflow         : hidden;
    padding          : 4px 20px 4px;
    color            : #fff;
    border-radius    : 0 3px 3px 0;
    background-color : rgba(0, 204, 82, 0.9);


    // .item-collection__title--big
    &--big {
      font-size : 32px;
      top       : 25px;
      padding   : 4px 30px 7px;
    }
  }

  // .item-collection__image
  &__image {
    transition : transform 0.3s;

    &:hover {
      transform : scale(1.1)
    }
  }
}


