.about-block {
  // .about-block__inner
  &__inner {
    display : flex;
  }

  // .about-block__img
  &__img {
    position     : relative;
    flex         : 0 0 auto;
    margin-right : 55px;

    &::after {
      content  : '';
      position : absolute;
      top      : 15px;
      right    : -15px;
      bottom   : - 15px;
      left     : 15px;
      border   : 4px solid #feb618;
    }
  }

  // .about-block__info
  &__info {
  }

  // .about-block__title
  &__title {
    font-size   : 24px;
    font-weight : bold;
    color       : #161616;
  }

  // .about-block__text
  &__text {
    font-family : 'Lato', sans-serif;
    font-size   : 16px;
    color       : #454545;
  }
}


.works-block {
  // .works-block__inner
  &__inner {
    display : flex;
  }

  // .works-block__item
  &__item {
    overflow : hidden;


    > img {
      display    : block;
      width      : 100%;
      transition : transform .3s;
    }

    &:hover {
      > img {
        transform : scale(1.1);
      }
    }
  }
}

